import React from "react";
import utils from "../../../utils/utils.json"
import Project from "./Project/Project";

const projects = ()=>{

  const works =  utils["projects"].map(project => {
    return (
      <Project 
          key={project.id} 
          imgUri = {project.imgUri} 
          title={project.title} 
          link={project.link} 
          source={project.source}
      />
  )})
  return (
    <section id="work" className="section">
      <div className="container">
        <div className="row sm-m-25px-b m-35px-b">
          <div className="col-md-12">
            <div className="section-title">
              <h3 className="dark-color text-uppercase">LATEST WORKS</h3>
              <p className="text-uppercase small">
                Pythonista, Blockchain Expert, Full-stack Developer
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="portfolio-content lightbox-gallery">
          {works}
        </div>
      </div>
    </section>
  );
}
export default projects;
