import React from "react";
import util from "../../../utils/utils.json"
import Service from "./Service/Service";

function Services() {

  const services = util["services"].map(service => {
      return (
        <Service 
          key={service.id}
          title={service.title}
          description={service.description}
          icon={service.icon || "icon-desktop"}
        />
      )
  })

  return (
    <section id="services" className="section gray-bg">
      <div className="container">
        <div className="row sm-m-25px-b m-35px-b">
          <div className="col-md-12">
            <div className="section-title">
              <h3 className="dark-color text-uppercase">My Services</h3>
              <p className="text-uppercase small">
                Pythonista, Blockchain Expert, Full-stack Developer
              </p>
            </div>
          </div>
        </div>
        <div className="row">
          {services}
        </div>
      </div>
    </section>
  );
}

export default Services;
